/* HERO QUOTE */
.hero-quote {
    display: block;
    padding: 1em;
    background-position: center;
    background-size: cover;
}

.hero-quote-text {
    display: block;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    min-height: 250px;
    padding: 1em;
    background-color: rgba(0,0,0,.5);
    color: color(white);
    text-align: center;
    @extend %serif;
    font-size: 1.25em;
    line-height: 1.4;

    &:before {
        margin: 2em auto 1.5em;
    }
    &:after {
        margin: 1.5em auto 2em;
    }
    &:after,
    &:before {
        content: '';
        display: block;
        height: 2px;
        width: 70%;
        background-color: color(white);
    }
    @include media($medium) {
        font-size: 1.5em;
    }
    @include media($large) {
      font-size: 1.75em;
      padding: 1em 17.5%;
    }
}


/*HERO OVERVIEW*/

// TODO Add these as drop-down options for module

.gray {
    background-color: #58595b;
}

.red {
    background-color: #dd674c;
}

.blue {
    background-color: #1277a0;
}

.hero-overview {
    padding: 1.5em;
    margin: 0;
    img {
      width: 100%;
      @include media($medium) {
        display: block;
        float: left;
        max-width: 35%;
      }
    }
    &:after {
      content: '';
      display: table;
      clear: both;
    }
}

// TODO Add color options for text and underline to module

.hero-overview-text {
    width: 100%;
    h1 {
      margin: 1.5em 0 0;
      color: color(secondary);
      font-size: 1rem;
      text-transform: uppercase;
      @include media($medium) {
          display: block;
          float: right;
          max-width: 65%;
          margin: 0 0 0.5em;
          padding-left: 1.5em;
      }
    }
    h2 {
        margin: 0 0 0.5em;
        color: color(white);
        font-size: 2rem;
        @extend %serif;
        font-weight: lighter;
    }
    p {
        color: color(white);
        margin-bottom: 1.5em;
    }
    // TODO Make underlines optional? Add contact field?
    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 6px;
      border-top: 2px solid color(secondary);
      border-bottom: 2px solid color(secondary);
    }
}
